html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  text-rendering: optimizeLegibility; }

html, body {
  height: 100%;
  margin: 0; }

html {
  overflow-y: scroll; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

.logo {
  cursor: pointer;
  position: absolute;
  width: 240px;
  height: 110px; }
  .logo img {
    position: absolute;
    width: 100%;
    height: 100%;
    -o-object-position: 0 -6px;
       object-position: 0 -6px;
    -o-object-fit: cover;
       object-fit: cover; }
  .logo span {
    position: absolute;
    color: #fff;
    z-index: 1;
    width: 100%;
    bottom: 0;
    line-height: 36px;
    text-align: center;
    text-decoration: underline; }

.logo:hover {
  font-weight: bold; }

.navtop {
  border-top: 30px solid #37f;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  background: #eee;
  border-bottom: 5px solid #eee; }
  .navtop .fill {
    width: 240px;
    height: 80px; }
  .navtop a {
    line-height: 80px;
    padding: 0 16px;
    color: #37f;
    font-weight: bold;
    font-size: 30px;
    text-decoration: none; }
    .navtop a:hover {
      text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3); }
  .navtop a.ham {
    padding: 0;
    width: 60px;
    width: calc(100% - 240px);
    max-width: 100px;
    text-align: center;
    color: #000;
    display: none; }
  .navtop a.link {
    background: #fff;
    color: #000;
    font-weight: normal;
    white-space: nowrap;
    text-align: center;
    padding: 22px;
    font-size: 14px;
    line-height: 18px;
    border-right: 1px solid #ddd; }
    .navtop a.link.oneline {
      padding: 31px 22px; }
  .navtop a.tel {
    -webkit-box-flex: 1;
            flex-grow: 1;
    text-align: right; }

@media (max-width: 1000px) {
  .navtop a.ham {
    display: block; }
  .navtop a.link {
    display: block;
    overflow: hidden;
    flex-basis: 100%;
    height: 0px;
    line-height: 14px;
    padding: 0;
    border: 0 solid #fff;
    position: relative;
    color: #fff;
    border-right: none;
    -webkit-box-ordinal-group: 5;
            order: 4; }
    .navtop a.link.oneline {
      padding: 0; }
    .navtop a.link br {
      display: none; }
  .navtop.animateHeight a.link {
    -webkit-transition: padding .25s;
    transition: padding .25s; }
  .navtop a.tel {
    -webkit-box-ordinal-group: 4;
            order: 3; }
  .navtop.expand a.link {
    color: #000;
    border-bottom: 1px solid #ddd;
    height: inherit;
    padding: 13px 0 9px 0; }
    .navtop.expand a.link:hover {
      background: #ddf;
      text-shadow: none; } }

section > h1 {
  margin: 20px 90px; }

@media (max-width: 900px) {
  section > h1 {
    margin: 20px 10vw; } }

article > .block {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 50px 90px; }
  article > .block .big-blurb {
    font-size: 27px;
    text-align: center;
    margin: 40px 0; }
  article > .block .red {
    color: #24e; }
  article > .block .small-blurb {
    font-size: 16px;
    line-height: 26px;
    margin: 40px 0; }
  article > .block .med-blurb {
    font-size: 19px;
    font-weight: 600;
    text-align: center;
    margin: 40px 0; }
  article > .block hr {
    width: 100%; }
  article > .block ul {
    padding: 0;
    margin: 0;
    width: 48%;
    width: calc(50% - 20px); }
  article > .block ul.left {
    float: left; }
  article > .block ul.right {
    float: right; }
  article > .block ul li {
    background: url(/i/heart-bullet.gif) no-repeat 0 3px;
    list-style: none;
    padding: 0 0 15px 30px; }
  article > .block .clear {
    clear: both;
    display: block; }

@media (max-width: 900px) {
  article > .block {
    margin: 50px 10vw; }
    article > .block ul {
      width: 100% !important; } }

.summary {
  background: #f0f0dc;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  text-align: center;
  overflow: auto;
  padding: 0 1em 4em 1em; }
  .summary .arrow {
    display: block;
    border-top: 30px solid white;
    border-left: 40px solid #f0f0dc;
    border-right: 40px solid #f0f0dc;
    margin-bottom: 3em;
    margin-left: auto;
    margin-right: auto;
    width: 0; }
  .summary .headline {
    font-size: 1.6em;
    font-weight: 600; }
  .summary .headline b {
    text-transform: uppercase; }
  .summary .headline i {
    font-style: normal;
    color: #24e; }
  .summary .item div {
    padding: 2em 2em 0 2em; }
  .summary .item b {
    font-weight: 600;
    display: block; }
  .summary .item i {
    font-weight: 300;
    font-size: 36px;
    font-style: normal;
    color: #24e;
    display: block; }

@media (min-width: 900px) {
  .summary .item {
    float: left;
    width: 33%; } }
